@import "common.scss";

body {
  position: relative;
}
/* TITLE */
#title {
  height: calc(100vh - 120px);
  position: relative;
  .wrap {
    height: 100%;
    width: 90%;
    position: relative;
  }
  .title_slide {
    width: calc(100% * 2);
    height: 100%;
    li {
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 90%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-left: 100px;
      box-sizing: border-box;
      position: absolute;
      &:nth-child(1) {
        background-image: url("../img/main_visual_01.jpg");
      }
      &:nth-child(2) {
        background-image: url("../img/main_visual_02.jpg");
      }
      a {
        margin-top: 50px;

        &::after {

        }
        &:hover::after {

        }
        &:hover span {

        }
        span {

          &::before {

          }
        }
      }
    }
    h3 {
      font-size: 60px;
      letter-spacing: 12px;
      font-weight: 200;
      line-height: 1.3;
      color: #333;
      margin-bottom: 30px;
    }
    p {
      font-weight: 200;
    }
  }
  .slide_count {
    button {

      &::before {

      }
      &.left::after {

      }
      &.right::after {

      }
    }
  }
  .slide_btn {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    gap: 7px;
    ul {
      display: flex;
      gap: 7px;
      align-items: center;
      li {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        &::before {
          content: "";
          display: block;
          width: 7px;
          height: 7px;
          background: #fff;
          border-radius: 50%;
          box-shadow: 1px 2px 1px rgba(0,0,0,.2);
        }
        &.active {
          border: 1px solid #fff;
          box-shadow: 1px 2px 1px rgba(0,0,0,.2);
        }
      } 
    }
    button {
      display: block;
      border: none;
      background: transparent;
      width: 20px;
      height: 20px;
      cursor: pointer;
      font-size: 0;
      &::before {
        content: "\f04b";
        @include web_font();
        color: #fff;
        font-size: 10px;
        text-shadow: 1px 2px 1px rgba(0,0,0,.2);
      }
    }
  }
}


